.App {
  text-align: center;
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.letter-button {
  margin: 5px;
  padding: 10px 15px;
  font-size: 18px;
  cursor: pointer;
}

.selected-letter {
  font-size: 120px;
  margin-top: 20px;
}
